.container {
    position: relative;
    width: 100%;
    /*max-width: 400px;*/
  }
  
  .container img {
    width: 100%;
    /*height: auto;*/
  }
  
  .container .btn {
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    background-color: #ef1111;
    color: rgb(247, 242, 242);
    font-size: 16px;
    padding: 12px 24px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
  }
  
  .container .btn:hover {
    background-color: black;
  }